          border-bottom-right-radius: 0px;
          margin-top: 0px;
          .totalSum {
            p:nth-of-type(2) {
              width: fit-content;
            }
          }
        }

        .currencyTableAll {
          width: 415px;
        }
      }

      .allinOneContainerRight {
        border-top: none;
        padding-left: calc((100vw - 820px - 460px) / 2);
        .homeSection {
          margin-top: 30px;
          .listCardsTabletContainer {
            width: 800px;
            margin-top: 80px;
            table {
              thead th:nth-of-type(4) {
                width: 300px;
              }

              tbody .cardDetails {
                width: 300px;
                color: wheat;
              }
            }
          }
        }
      }
    }
  }
}

